import React , {useState} from 'react'


function Heart({id}) {
        const [counter, setCounter] = useState(localStorage.getItem(id) | 0);
        const counterHandler = () => {
            setCounter(counter + 1)
        };
        const unCounterHandler = () => {
            setCounter(counter - 1)
        };
        localStorage.setItem(id, counter);
    return (
        <div className="d-inline-block ms-2 ">
          <a href="#" onClick={counterHandler} >
              <img className="me-2" width="20px" src="data:image/svg+xml;base64," />
              </a>
              {counter}
              <a href="#" onClick={unCounterHandler} >
              <img className="ms-2" width="20px"src="data:image/svg+xml;base64," />
              </a>
            
        </div>
    )
}

export default Heart
